import qs from 'qs'
export default {
    name: "membershow",
    data(){
        return{
            name:"",
            phone:"",
            school:""
        }
    },
    mounted:function () {
        this.$axios.post('/user/card_store',
            qs.stringify({
                user_id:"",
                card_id:"",
                name:this.name,
                tel:this.phone,
                university_id:this.school
            })).then(res=>{

        })
    }
}



